import React from 'react';
import ReactDOM from 'react-dom';


class Child2 extends React.Component {
    constructor(props) {
    super(props);
}
    uuu(event){
    console.log(event)
}
    render(){
      return (
           <div onClick={this.uuu}>
           <input onChange={this.props.handleEmail}/>
           </div>
        )
    }
};

//event.target.value
export default class Parent2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            email: ""
        };
    }
    getInitialState(){
        return {
            email: ''
        }
    }
    handleEmail(event){
        this.setState({email: event.target.value});
    }
    render(){
        return (
            <div>
                <div>2222222222{this.state.email}</div>
                <Child2 name="email" handleEmail={this.handleEmail.bind(this)}/>
            </div>
            )
    }
};




